<template>
  <section>
    <div v-if="loading" class="animate">
      <div class="orbit-spinner">
        <div class="orbit"></div>
        <div class="orbit"></div>
        <div class="orbit"></div>
     </div>
    </div>

    <div v-if="error" class="animate" style="text-align: center;">
      <h1>{{error}}</h1>
      <router-link to="/">Go Back</router-link>
    </div>

    <div v-if="profileData" class="container">
      <h1 class="gamertag">
        <img :src="profileData.platformInfo.avatarUrl" class="platform-avatar" />
        {{profileData.platformInfo.platformUserId}}
      </h1>
      <div class="grid">
        <div class="img-container">
          <div>
              <div class="top-centered">
              <h4>Active Legend</h4>
              <p>{{profileData.metadata.activeLegendName}}</p>
              </div>
              <img :src="profileData.segments[1].metadata.imageUrl" alt="">
          </div>
        </div>
        <div>
          <ul>
            <li v-if="profileData.segments[0].stats.season5Wins">
              <h4>Season 5 Win</h4>
              <p>
                {{profileData.segments[0].stats.season5Wins.displayValue}}
                <span>({{profileData.segments[0].stats.season5Wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.season4Wins">
              <h4>Season 4 Win</h4>
              <p>
                {{profileData.segments[0].stats.season4Wins.displayValue}}
                <span>({{profileData.segments[0].stats.season4Wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.season3Wins">
              <h4>Season 3 Win</h4>
              <p>
                {{profileData.segments[0].stats.season3Wins.displayValue}}
                <span>({{profileData.segments[0].stats.season3Wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.season2Wins">
              <h4>Season 2 Win</h4>
              <p>
                {{profileData.segments[0].stats.season2Wins.displayValue}}
                <span>({{profileData.segments[0].stats.season2Wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.season1Wins">
              <h4>Season 1 Win</h4>
              <p>
                {{profileData.segments[0].stats.season1Wins.displayValue}}
                <span>({{profileData.segments[0].stats.season1Wins.percentile}}%)</span>
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.level">
              <h4>Apex Level</h4>
              <p>
                {{profileData.segments[0].stats.level.displayValue}}
              </p>
            </li>
            <li v-if="profileData.segments[0].stats.damage">
              <h3 style="text-align: center;">LifeTime Stats</h3>
              <span>Damge</span>
              <p>
                {{profileData.segments[0].stats.damage.displayValue}}
                <span>({{profileData.segments[0].stats.damage.percentile}}%)</span>
              </p>
              <span>Kills</span>
              <p>
                {{profileData.segments[0].stats.kills.displayValue}}
              </p>
            </li>
          </ul>
        </div>
      </div>
      <router-link to="/">Go Back</router-link>
    </div>
  </section>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Profile',
  data () {
    return {
      loading: false,
      error: null,
      profileData: null
    }
  },
  beforeCreate () {
    document.body.className = 'body-bg-no-image'
  },
  async created () {
    this.loading = true
    try {
      const res = await axios.get(`/api/v1/profile/${this.$route.params.platform}/${this.$route.params.gamertag}`)

      this.profileData = res.data.data
      console.log(this.profileData)
      this.loading = false
    } catch (err) {
      this.loading = false
      this.error = err.response.data.message
    }
  }
}
</script>

<style scoped>
.img-container {
  position: relative;
  text-align: center;
  color: white;
}

.top-centered {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.container {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  margin: 1rem auto;
  padding: 2rem 1.5rem;
  border-radius: 20px;
}
h1.gamertag {
  font-size: 2rem;
  background: rgba(0, 0, 0, 0.6);
  padding: 0.3rem 0.5rem;
  text-align: center;
  border-radius: 20px;
  margin-bottom: 3rem;
  display: flex;
  align-items: center;
}
a {
  display: inline-block;
  margin-top: 2rem;
  border: #fff 2px solid;
  padding: 0.5rem 0.8rem;
  border-radius: 10px;
}
a:hover {
  border: #ccc 2px solid;
  color: #ccc;
}
.platform-avatar {
  width: 40px;
  margin-right: 0.7rem;
  border-radius: 10px;
}
img {
  width: 100%;
}
.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
}
li {
  background: rgba(0, 0, 0, 0.6);
  padding: 1rem;
  margin-bottom: 0.7rem;
  border-radius: 10px;
}
li p {
  font-size: 36px;
}
li span {
  font-size: 18px;
  color: #ccc;
}
@media (max-width: 500px) {
  h1 {
    font-size: 1.5rem;
    display: block;
    text-align: center;
  }
  .platform-avatar {
    display: none;
  }
  .grid {
    grid-template-columns: 1fr;
  }
  li p {
    font-size: 1.5rem;
  }
}

.animate {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.orbit-spinner, .orbit-spinner * {
      box-sizing: border-box;
    }

    .orbit-spinner {
      height: 55px;
      width: 55px;
      border-radius: 50%;
      perspective: 800px;
    }

    .orbit-spinner .orbit {
      position: absolute;
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }

    .orbit-spinner .orbit:nth-child(1) {
      left: 0%;
      top: 0%;
      animation: orbit-spinner-orbit-one-animation 1200ms linear infinite;
      border-bottom: 3px solid #fff;
    }

    .orbit-spinner .orbit:nth-child(2) {
      right: 0%;
      top: 0%;
      animation: orbit-spinner-orbit-two-animation 1200ms linear infinite;
      border-right: 3px solid #fff;
    }

    .orbit-spinner .orbit:nth-child(3) {
      right: 0%;
      bottom: 0%;
      animation: orbit-spinner-orbit-three-animation 1200ms linear infinite;
      border-top: 3px solid #fff;
    }

    @keyframes orbit-spinner-orbit-one-animation {
      0% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
      }
    }

    @keyframes orbit-spinner-orbit-two-animation {
      0% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
      }
    }

    @keyframes orbit-spinner-orbit-three-animation {
      0% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
      }
      100% {
        transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
      }
    }
</style>
